html,
body {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  background-color: #333;
  width: 100%;
}
.container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0.05) 100%);
  color: #333;
  position: relative;
  transition-duration: 0.1s;
  width: 100%;
}
.container:hover {
  color: #000;
  transition-duration: 0.3s;
}
.container .inner {
  padding: 10px 30px;
}
.container h1,
.container h3,
.container h4 {
  font-family: 'Droid Serif', verdana;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
.container h1 {
  font-size: 36px;
}
.container h3 {
  font-size: 26px;
}
.container div,
.container p {
  font-family: 'Open Sans', arial;
  font-size: 18px;
  padding: 0;
}
.container a {
  color: #333;
  text-decoration: underline;
}
.container a:visited {
  color: #333;
}
.container a:hover {
  color: #555;
}
.container ul {
  font-family: 'Open Sans', arial;
  font-size: 18px;
  list-style-type: none;
  margin: 0 0 20px 0;
  padding: 0;
}
.container ul.presentation_img_wrapper li {
  display: inline-block;
  font-size: 14px;
  list-style-type: none;
  margin: 0 2px 0 0;
  padding: 0;
}
.container ul.presentation_img_wrapper li a {
  text-decoration: none;
}
.container ul.presentation_img_wrapper li .presentation_img {
  background-position: 50% 30%;
  background-repeat: repeat;
  background-size: cover;
  border-radius: 50px;
  display: inline-block;
  height: 100px;
  position: relative;
  width: 100px;
  z-index: 2;
}
.container ul.presentation_img_wrapper li .presentation_img:after {
  border-radius: 50px;
  opacity: 0.5;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0.95) 100%);
  content: '';
  height: 100%;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 2;
}
.container ul.presentation_img_wrapper li .presentation_img:hover:after {
  transition: opacity 0.5s;
  opacity: 0.2;
}
.container ul.presentation_img_wrapper li .presentation_img_text {
  margin-top: 5px;
  text-align: center;
  font-weight: bold;
}
.container.container_1 {
  background-color: #a8b6bf;
}
.container.container_1:hover {
  background-color: rgba(168, 182, 191, 0.95);
}
.container.container_2 {
  background-color: #c9d8c5;
}
.container.container_2:hover {
  background-color: rgba(201, 216, 197, 0.95);
}
.container.container_3 {
  background-color: #edd9c0;
}
.container.container_3:hover {
  background-color: rgba(237, 217, 192, 0.95);
}
.container.container_4 {
  background-color: #bcd5d1;
}
.container.container_4:hover {
  background-color: rgba(188, 213, 209, 0.95);
}
.container.container_5 {
  background-color: #ba9077;
}
.container.container_5:hover {
  background-color: rgba(186, 144, 119, 0.95);
}
.container.container_6 {
  background-color: #729f98;
}
.container.container_6:hover {
  background-color: rgba(114, 159, 152, 0.95);
}
.container.container_7 {
  background-color: #b0aac2;
}
.container.container_7:hover {
  background-color: rgba(176, 170, 194, 0.95);
}
@media all and (max-width: 400px) {
  .container h1 {
    font-size: 30px;
  }
  .container h3 {
    font-size: 24px;
  }
  .container p {
    font-size: 16px;
  }
}
